import '../css/modalButton.scss'
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import { useState } from 'react';

function MyVerticallyCenteredModal(props) {
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {props.button.title}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <h4>{props.button.img}</h4>
        <p>
          {props.button.desc}
        </p>
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={props.onHide}>Close</Button>
      </Modal.Footer>
    </Modal>
  );
}


export function ModalButton() {
  const [modalShow, setModalShow] = useState(false);
  const [activeButton, setActiveButton] = useState({})

  return <div className="buttonContainer">
    <Button variant="danger" onClick={() => {
      setActiveButton({ title: "Our Chef", desc: "Hallo", img: "This will be a picture" })
      setModalShow(true)
    }}>
      Our Chef
    </Button>
    <Button variant="danger" onClick={() => {
      setActiveButton({ title: "Truck location", desc: "Hallo", img: "This will be a picture" })
      setModalShow(true)
    }}>
      Truck location
    </Button>
    <Button variant="danger" onClick={() => {
      setActiveButton({ title: "Reviews", desc: "Hallo", img: "This will be a picture" })
      setModalShow(true)
    }}>
      Reviews
    </Button>

    <MyVerticallyCenteredModal
      button={activeButton}
      show={modalShow}
      onHide={() => setModalShow(false)}
    />

  </div>
}

export default ModalButton